@import url(https://fonts.googleapis.com/css2?family=KoHo:ital,wght@0,300;0,400;0,500;0,600;0,700;1,200;1,300;1,400;1,500;1,600;1,700&family=Pattaya&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.skill-list:hover{background-color:#fff8e2 !important}.course-title:hover{color:#d8ac1b}

.modal-90w{width:90vw !important;max-width:90vw !important}.modal-90w .modal-dialog{max-width:90vw !important;width:90vw !important}.miniature{font-family:'Pattaya', sans-serif;font-family:'KoHo', sans-serif;cursor:pointer !important}.shadow-drop-center:hover{animation:shadow-drop-center 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both !important}.course-modal{font-family:'KoHo', sans-serif}.course-modal img{width:30vw}@keyframes shadow-drop-center{0%{box-shadow:0 0 0 0 rgba(0,0,0,0)}100%{box-shadow:0 0 20px 0px rgba(0,0,0,0.35)}}

